// header

body.landing {
  header.banner {
    top: $vmargin;
    @include bp-m {top: 2.5rem;}

    div.wrap {

      p.logo {
        margin: 0 auto;
        width: 250px;
        background: url(../../assets/images/logos/inverted-mantra-systems.svg) no-repeat 50% 100%; 
        background-size: 100% auto;

        @include bp-m {width: 300px;}

        &:before {
          display: block;
          padding-top: 13%;
          content: "";
        }

        span {@include hide-screen();}
      }
    }
  }
}